.Modal {
  position: fixed;
  z-index: 500;
  background-color: white;
  width: 70%;
  padding: 30px;
  box-shadow: 7px 7px 16px rgba(0, 0, 0, 0.15);
  border-radius: 11px;
  left: 15%;
  top: 30%;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
}

.Modal .img {
  padding: 20px 0;
}
.Modal img {
  display: block;
  max-width: 100%;
}
@media (min-width: 600px) {
  .Modal {
    width: 500px;
    left: calc(50% - 250px);
  }
}
