@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,900);
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.table {
  width: 100%;
  font-size: 14px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.15);
  border-radius: 7px;
  margin-bottom: 10px;
}

.table th {
  background-color: #263345;
  border-right: 1px solid rgba(255, 255, 255, 0.2);
  text-align: center;
  color: #a4b5c6;
  padding: 8px 10px;
  font-weight: bold;
}
.table th:first-child {
  border-radius: 7px 0 0;
}
.table th:last-child {
  border-radius: 0 7px 0 0;
  border-right: none;
}

.table td {
  padding: 15px 0px;
  text-align: center;
  vertical-align: middle;
  color: #263345;
}

.table td:first-child {
  cursor: pointer;
}

.table td:not(:last-child) {
  border-right: 1px solid #ebedee;
}
table td:last-child,
table td:nth-last-child(2) {
  padding: 0;
}
.table tbody tr:nth-child(odd) {
  background-color: #f5f5f5;
}
.table tr {
  height: 25px;
}

.table tbody tr:last-child td:first-child {
  border-radius: 0 0 0 7px;
}
.table tbody tr:last-child td:last-child {
  border-radius: 0 0 7px 0;
}

.btn-icon {
  background-color: transparent;
  border: none;
  font-size: 16px;
  cursor: pointer;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.btn-icon.delete {
  color: #ea4040;
}
.btn-icon.delete:hover {
  color: #cd3b3b;
}

.btn-icon.edit {
  color: #263345;
}

.btn-icon.edit:hover {
  color: #1f2b3a;
}

.flex {
  display: flex;
  align-items: center;
}

.justify-end {
  justify-content: flex-end;
}

.avatar {
  color: #fff;
  display: flex;
  align-items: center;
  margin-right: 30px;
}
.avatar img {
  margin-right: 5px;
}

.navbar {
  padding: 10px 30px;
  background-color: #253344;
  box-shadow: 1px 3px 10px 4px rgba(0, 0, 0, 0.15);
}
.navbar .logo img {
  width: 220px;
}
.navbar .logo:hover img {
  opacity: 0.8;
}

.btn {
  padding: 0 20px;
  height: 40px;
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
  background-color: #2ca5ee;
  border-radius: 7px;
  box-shadow: 3px 1px 9px 1px rgba(0, 0, 0, 0.25);
  border: none;
  color: #fff;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  cursor: pointer;
}

.btn span {
  vertical-align: middle;
}
.btn .plus {
  font-size: 24px;
  margin-right: 10px;
}

.btn:hover {
  background-color: #2c9ce6;
  box-shadow: 3px 1px 12px 3px rgba(0, 0, 0, 0.25);
}

.link {
  text-decoration: none;
}

.form-style-6 {
  font: 95% Arial, Helvetica, sans-serif;
  max-width: 400px;
  margin: 10px auto;
  padding: 16px;
  background: #f7f7f7;
}
.form-style-6 h1 {
  background: #43d1af;
  padding: 20px 0;
  font-size: 140%;
  font-weight: 300;
  text-align: center;
  color: #fff;
  margin: -16px -16px 16px -16px;
}
.form-style-6 input[type="text"],
.form-style-6 input[type="date"],
.form-style-6 input[type="datetime"],
.form-style-6 input[type="email"],
.form-style-6 input[type="number"],
.form-style-6 input[type="search"],
.form-style-6 input[type="time"],
.form-style-6 input[type="url"],
.form-style-6 textarea,
.form-style-6 select {
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  outline: none;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  width: 100%;
  background: #fff;
  margin-bottom: 4%;
  border: 1px solid #ccc;
  padding: 3%;
  color: #555;
  font: 95% Arial, Helvetica, sans-serif;
}
.form-style-6 input[type="text"]:focus,
.form-style-6 input[type="date"]:focus,
.form-style-6 input[type="datetime"]:focus,
.form-style-6 input[type="email"]:focus,
.form-style-6 input[type="number"]:focus,
.form-style-6 input[type="search"]:focus,
.form-style-6 input[type="time"]:focus,
.form-style-6 input[type="url"]:focus,
.form-style-6 textarea:focus,
.form-style-6 select:focus {
  box-shadow: 0 0 5px #43d1af;
  padding: 3%;
  border: 1px solid #43d1af;
}

.form-style-6 input[type="submit"],
.form-style-6 input[type="button"] {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  width: 100%;
  padding: 3%;
  background: #43d1af;
  border-bottom: 2px solid #30c29e;
  border-top-style: none;
  border-right-style: none;
  border-left-style: none;
  color: #fff;
}
.form-style-6 input[type="submit"]:hover,
.form-style-6 input[type="button"]:hover {
  background: #2ebc99;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.upload::before {
  content: "Բեռնել";
  display: inline-block;
  background: -webkit-gradient(linear, left bottom, left top, from(#bbdce2), to(#e3e3e3));
  background: -webkit-linear-gradient(bottom, #bbdce2, #e3e3e3);
  background: linear-gradient(to top, #bbdce2, #e3e3e3);
  border: 1px solid #999;
  border-radius: 3px;
  padding: 5px 8px;
  outline: none;
  white-space: nowrap;
  -webkit-user-select: none;
  cursor: pointer;
  text-shadow: 1px 1px #fff;
  font-weight: 700;
  font-size: 10pt;
}

.upload::-webkit-file-upload-button {
  visibility: hidden;
}

.upload:hover::before {
  border-color: black;
}
.upload:active::before {
  background: -webkit-linear-gradient(top, #b4b3fd, #f9f9f9);
}

.uploads {
  display: none;
}


.image-upload > input
{
    display: none;
}

.image-upload img
{
    width: 80px;
    cursor: pointer;
}
.Modal {
  position: fixed;
  z-index: 500;
  background-color: white;
  width: 70%;
  padding: 30px;
  box-shadow: 7px 7px 16px rgba(0, 0, 0, 0.15);
  border-radius: 11px;
  left: 15%;
  top: 30%;
  box-sizing: border-box;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.Modal .img {
  padding: 20px 0;
}
.Modal img {
  display: block;
  max-width: 100%;
}
@media (min-width: 600px) {
  .Modal {
    width: 500px;
    left: calc(50% - 250px);
  }
}

.Backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

